  border: none;
  cursor: pointer;
  position: relative;
  background-color: transparent;
}
.icon-button .icon-button-hover {
  position: absolute;
  background-color: #222;
  color: white;
  padding: 5px 6px;
  opacity: 0;
  width: max-content;
  max-width: 130px;
  pointer-events: none;
  z-index: 1;
}
.icon-button > *:not(.icon-button-hover) {
  display: flex;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 17px;
  align-items: center;
  justify-content: center;
  color: var(--font-color);
}
.icon-button-hover.bottom {
  left: 50%;
  bottom: -85%;
  transform: translateX(-50%);
}
.icon-button-hover.top {
  left: 50%;
  top: -85%;
  transform: translateX(-50%);
}
.icon-button-hover.rigth {
  left: 50px;
  top: 50%;
  transform: translateY(-50%);
}
.icon-button-hover.left {
  right: 45px;
  top: 50%;
  transform: translateY(-50%);
}
.icon-button:hover .icon-button-hover {
  opacity: 1;
}

.icon-button.main > *:not(.icon-button-hover) {
  background-color: var(--main-color);
  color: white;
}
.icon-button.body-color > *:not(.icon-button-hover) {
  background-color: var(--body-color);
}
.icon-button.secondry-color > *:not(.icon-button-hover) {
  background-color: var(--section-color);
}
.icon-button:not(.main):hover > *:not(.icon-button-hover) {
  background-color: var(--border-color);
}
.icon-button-hover::before {
  content: "";
  position: absolute;
  background: black;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
.icon-button-hover.bottom::before {
  left: 50%;
  transform: translateX(-50%);
  top: -3px;
}
.icon-button-hover.top::before {
  left: 50%;
  transform: translateX(-50%);
  bottom: -3px;
}
.icon-button-hover.left::before {
  top: 50%;
  transform: translateY(-50%);
  right: -3px;
}
.icon-button-hover.right::before {
  top: 50%;
  transform: translateY(-50%);
  left: -3px;
}
